<template>
  <div class="ui center aligned fluid container" style="max-width: 60em !important">
    <h1>{{ $t('cart') }}</h1>
    <div v-if="cartIsEmpty">
      <div class="errMessage">{{ $t('cartIsEmpty') }}</div>
    </div>
    <div v-else>
      <table class="ui very basic padded unstackable table">
        <thead>
        <tr>
          <th class="one wide">{{ $t('preview') }}</th>
          <th class="thirteen wide">{{ $t('product') }}</th>
          <th class="two wide center aligned">{{ $t('price') }}</th>
        </tr>
        </thead>
        <LineItem v-for="item in lineItems" v-bind:key="item.productId" v-bind:item="item" />
        <tr>
          <th class="right aligned" colspan="2">{{$t('currentSum')}}</th>
          <th class="center aligned">{{ parseFloat(cartSum).toFixed(2) }}€</th>
        </tr>
      </table>

      <CartNavButtons 
        v-bind:checkoutURL="checkoutURL" 
        v-bind:cartIsEmpty="cartIsEmpty" 
        v-bind:lineItems="lineItems">
      </CartNavButtons>
    </div>
    
  </div>
</template>

<script>
import LineItem from "./LineItem.vue";
import CartNavButtons from "./CartNavButtons.vue";

export default {
  props: {
    lineItems: Array,
    checkoutURL: String,
    lineItemsLoaded: Boolean
  },
  computed: {
    cartSum: function() {
      return this.lineItems.reduce(
        (sum, currentItem) => sum + currentItem.price,
        0
      );
    },
    cartIsEmpty: function(){
      return (this.lineItemsLoaded && this.lineItems.length === 0)
    }
  },

  components: {
    LineItem,
    CartNavButtons,
  }
};
</script>

