import language from './languageSelect'

const host = window.location.host

let editorUrl = "https://editor."+host
let checkoutUrlPrefix = "https://secure."+host

const getHostTail = () => host.replace('cart.', '').replace('https://', '');

if (window.location.host.includes("localhost")) {
    editorUrl = "http://"+window.location.host.split(':')[0]+":8080"
    checkoutUrlPrefix = "https://secure.thestarposter.com.yoursky.space"
}

if (window.location.host.includes("cart.nachthimmel.shop")) {
    const hostTail = getHostTail(host);

    editorUrl = "https://editor."+hostTail
    checkoutUrlPrefix = "https://"+hostTail
}

export {
    editorUrl,
    checkoutUrlPrefix,
    language
}
