const cartBaseUrl = process.env.VUE_APP_STARMAP_BACKEND_URL;

let cartProvided = undefined

export async function initialize(cartData) {
  cartProvided = cartData
  const urlParameters = getUrlParameters();

  if (urlParameters.productId) {
    // seems to contain a product
    const lineItem = {
      productId: urlParameters.productId,
      productType: urlParameters.productType,
      lineItemProperties: urlParameters.lineItemProperties,
    };

    const putResponse = await fetch(
      cartBaseUrl + '/cart/' + lineItem.productId,
      {
        method: 'PUT',
        credentials: 'include',
        headers: {
          'Content-Type': 'application/json; charset=utf-8',
        },
        body: JSON.stringify(lineItem),
      }
    );

    if (putResponse.status > 400) {
      throw new Error('Could not add item: ' + (await putResponse.text()));
    }
    else {
      // remove url parameters for adding product from url, so reload wont readd a cart item
      if (history.replaceState) {
        history.replaceState(null, '', '?')
      }
    }
  }
}

export async function refreshCart() {
  const getCartResponse = await fetch(cartBaseUrl + '/cart/' + window.lng, {
    method: 'GET',
    credentials: 'include',
  });
  const cartFromServer = await getCartResponse.json();
  cartProvided.lineItems = Object.values(cartFromServer.lineItems);
  cartProvided.lineItemsLoaded = true;
}

export async function refreshCheckoutURL() {
  // setting checkoutURL to undefined because we don't want the user to checkout with a stale cart while the update loads
  cartProvided.checkoutURL = undefined;
  const res = await fetch(cartBaseUrl + '/cart/checkout/' + window.lng, {
    method: 'GET',
    credentials: 'include',
  });
  const url = await res.text();
  cartProvided.checkoutURL = url;
}

export async function deleteItemfromCart(productId) {
  await fetch(`${cartBaseUrl}/cart/${productId}`, {
    method: 'DELETE',
    credentials: 'include',
  });
  cartProvided.lineItemsLoaded = true
  refreshCart()
  refreshCheckoutURL()
}

function getUrlParameters() {
  const parameters = {};
  const search = decodeURIComponent(location.search.substring(1));

  search.split('&').forEach(raw => {
    const key = raw.split('=')[0];
    const value = raw.split('=')[1];
    if (key.includes('[')) {
      const splittedKey = key.split('[');
      const parentKey = splittedKey[0];
      const childKey = splittedKey[1].substring(0, splittedKey[1].length - 1);
      if (parameters[parentKey] === undefined) {
        parameters[parentKey] = {};
      }
      parameters[parentKey][childKey] = value;
    } else {
      parameters[key] = value;
    }
  });
  return parameters;
}
