import Vue from 'vue'
import App from './App.vue'
import i18next from 'i18next';
import XHR from 'i18next-xhr-backend';
import VueI18Next from '@panter/vue-i18next';
import * as cartProvider from "./providers/CartProvider";
import {language} from "./util/config"

Vue.config.productionTip = false
Vue.use(VueI18Next);
Vue.prototype.$cartProvider = cartProvider;
window.lng = language;

const i18nextOptions = {
  backend: {
    loadPath: process.env.VUE_APP_STARMAP_BACKEND_URL+'/translations/{{lng}}/{{ns}}.json'
  },
  ns: ['editor', 'starmap'],
  defaultNS: 'editor',
  lng: window.lng,
  fallbackLng: 'de',
  debug: false,
}

i18next
  .use(XHR)
  .init(i18nextOptions);
const i18n = new VueI18Next(i18next);

new Vue({
  render: h => h(App),
  i18n: i18n
}).$mount('#app')
