<template>
  <div class="ui left aligned container">
    <div v-if="!cartIsEmpty" class="underPrices">
      <div class="ui checkbox legalChecks">
        <input type="checkbox" @click="toggleAGB">
        <label>{{$t('agreeToTermsCheckbox.pre')}}<a :href="$t('agreeToTermsCheckbox.linkHref')">{{$t('agreeToTermsCheckbox.linkDesc')}}</a>{{$t('agreeToTermsCheckbox.post')}}</label>
      </div>
      <div v-if="digitalProductsInCard" class="ui checkbox legalChecks">
        <input type="checkbox" @click="toggleCancellationDisclaimer">
        <label for="agree1">{{$t('agreeCancellationCheckbox')}}</label>
      </div>
      <div class="cart__nav-buttons">
        <a v-bind:href=newPosterUrl>
          <button class="ui button">
            {{$t('addProduct')}}
          </button>
        </a>
        <a v-bind:href=buyNowUrl v-bind:class="{ loading: checkoutURL === undefined }">
          <button class="ui primary button " :disabled=isCheckoutButtonDisabled>
          {{$t('checkout')}} 
          </button>
        </a>
      </div>
    </div>
    <div v-else class="cart__nav-button" id="btnAddFirstProduct">
      <a v-bind:href=newPosterUrl>
        <button class="button">
          {{$t('addFirstProduct')}}
        </button>
      </a>
    </div>
  </div>
</template>

<script>
import { editorUrl, checkoutUrlPrefix } from "../util/config.js";

export default {
  name: "CartNavButtons",
  data() {
    return {
      isAGBChecked: false,
      isCancellationDisclaimerChecked: false
    };
  },
  methods: {
    toggleAGB() {
      this.isAGBChecked = !this.isAGBChecked;
    },
    toggleCancellationDisclaimer() {
      this.isCancellationDisclaimerChecked = !this
        .isCancellationDisclaimerChecked;
    }
  },
  props: {
    checkoutURL: String,
    lineItems: Array,
    cartIsEmpty: Boolean
  },
  computed: {
    newPosterUrl: function() {
      return editorUrl;
    },
    buyNowUrl: function() {
      return `${checkoutUrlPrefix}${this.checkoutURL}`;
    },
    isCheckoutButtonDisabled: function() {
      // if AGB not checked always disable, when only PDFs in cart check for Disclaimer
      if (!this.isAGBChecked) {
        return true;
      }
      return (
        this.digitalProductsInCard && !this.isCancellationDisclaimerChecked
      );
    },
    digitalProductsInCard: function() {
      return (
        this.lineItems.find(l => l.fulfillment === "digital") !== undefined
      );
    }
  }
};
</script>

<style>
.cart__nav-buttons {
  margin: 2em 0 2em 0;
  text-align: center;
}
.loading {
  pointer-events: none !important;
  cursor: default !important;
}
.underPrices {
  float: right;
  max-width: 30em;
}
#btnAddFirstProduct {
  float: left;
}
</style>