<template>
  <div id="app">
    <Cart 
      v-bind:lineItems="lineItems"
      v-bind:checkoutURL="checkoutURL"
      v-bind:lineItemsLoaded="lineItemsLoaded">
    </Cart>
  </div>
</template>

<script>
  import Cart from "./components/Cart.vue";

  export default {
    name: "app",
    data: function() {
       return {
         lineItems: [],
         checkoutURL: undefined,
         lineItemsLoaded: false
       }
    },

    beforeCreate: async function () {
      await this.$cartProvider.initialize(this)
      // This is intentionally not async because we load the checkout 
      this.$cartProvider.refreshCheckoutURL()
      await this.$cartProvider.refreshCart()
    },
    components: {
      Cart,
    }
  };
</script>

