<template>
<tbody>
  <tr>
    <td class="collapsing">
      <img v-show="isImgLoaded" v-on:click="showPreviewModal" v-bind:src=imageUrl @load="isImgLoaded = true" class="lineItem__thumbnail">
      <div v-show="!isImgLoaded"  class="ui active inline loader"></div>
    </td>
    <td>
      <div class="lineItem__title">{{ titleKey }}</div>
      <div class="lineItem__editorProperties-container">
        <div v-for="(item, key) in item.editorProperties" v-bind:key="key">
          {{ key }}: {{ item }}
        </div>
      </div>
    </td>
    <td class="center aligned">
      {{ parseFloat(item.price).toFixed(2)}}€
    </td>
  </tr>
  <tr>
    <td colspan="3">
      <div class="lineItem__button--container">
        <button v-on:click="showPreviewModal" :disabled=!isImgLoaded class="button primary">
          {{$t('preview')}}
        </button>
        <a v-bind:href=modifyPosterUrl>
          <button class="button">
            {{$t('edit')}}
          </button>
        </a>
        <button v-on:click="removePosterFromCart" class="button">
          {{$t('delete')}}
        </button>
     </div>
    </td>
  </tr>
  <PreviewModal v-if="isPreviewModalVisible" v-bind:imageUrl="imageUrl" @close="closePreviewModal">></PreviewModal>
</tbody>
</template>


<script>
import PreviewModal from "./PreviewModal.vue";
import { editorUrl } from "../util/config.js";

export default {
  name: "LineItem",
  props: {
    item: Object
  },
  data() {
    return {
      isPreviewModalVisible: false,
      isDeleted: false,
      isImgLoaded: false
    };
  },
  methods: {
    showPreviewModal() {
      this.isPreviewModalVisible = true;
    },
    closePreviewModal() {
      this.isPreviewModalVisible = false;
    },
    removePosterFromCart() {
      this.$cartProvider.deleteItemfromCart(this.item.productId);
    }
  },

  computed: {
    imageUrl: function() {
      //todo adjust for different product names
      return `${process.env.VUE_APP_STARMAP_BACKEND_URL}/posters/${
        this.item.productId
      }/preview.jpg`;
    },
    modifyPosterUrl: function() {
      // This productId thing is just a temp solution to support legacy
      const shopifyProductId =
        this.item.lineItemProperties.delivery === "PDF"
          ? "1030596624428"
          : "7030690480172";
      return `${editorUrl}/?edit=true&productId=${shopifyProductId}&posterId=${
        this.item.productId
      }`;
    },
    titleKey: function() {
      // this means that for now every line item property has to change the title. Also it is unclear if the ordering will work like this. We have to see how this works for more complicated products
      const properties = Object.values(this.item.lineItemProperties).join(".");
      return this.$t(
        this.item.productType + ":product." + properties + ".title"
      );
    }
  },
  components: {
    PreviewModal
  }
};
</script>

